import React from "react";
import { Link, useStaticQuery, graphql } from "gatsby";
import "./layout.scss";
import { StaticImage } from "gatsby-plugin-image";

interface Data {
  site: {
    siteMetadata: {
      title: string;
      titleImg: string;
      nav: { name: string; path: string }[];
    };
  };
}
const Layout = ({
  pageTitle,
  children,
}: {
  pageTitle: string;
  children: React.ReactNode | React.ReactNode[];
}) => {
  const data: Data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          titleImg
          title
          nav {
            name
            path
          }
        }
      }
    }
  `);
  console.log(data);
  return (
    <div className="container">
      <title>
        {pageTitle} | {data.site.siteMetadata.title}
      </title>
      <header>
        <Link to="/">
          <img
            // src={data.site.siteMetadata.titleImg}
            src="/logo4.png"
            alt="cat"
            className="logo"
            draggable="false"
            onMouseEnter={(e: React.MouseEvent): void => {
              (e.target as HTMLImageElement).src = `/logo${
                Math.random() >= 0.5 ? "2" : "3"
              }.png`;
            }}
            onMouseLeave={(e: React.MouseEvent): void => {
              (e.target as HTMLImageElement).src = `/logo4.png`;
            }}
          />
          <img
            // src={data.site.siteMetadata.titleImg}
            src={data.site.siteMetadata.titleImg}
            alt={data.site.siteMetadata.title}
            className="title-img"
            draggable="false"
          />
        </Link>
      </header>
      <nav>
        <ul className="nav-links">
          {data.site.siteMetadata.nav.map((item, i) => (
            <li key={i} className="nav-link-item">
              <Link
                to={item.path}
                className={`nav-link-text ${
                  `${window.location.href}` === `${window.origin}${item.path}`
                    ? "current"
                    : ""
                }`}
              >
                {item.name}
              </Link>
            </li>
          ))}
        </ul>
      </nav>
      <main>
        <h1 className="heading">{pageTitle}</h1>
        {children}
      </main>
    </div>
  );
};

export default Layout;
